
import globals from './global';
import entity from './entities';
import React from 'react';

import{Navbar,Nav,NavDropdown}  from 'react-bootstrap';





class Menus extends React.Component {
    constructor(props){
      super(props);
      this.disconnect=this.disconnect.bind(this);
      this.state={menuChanged:false};
    }
    componentDidUpdate(){
      if(this.state.menuChanged==true){
        this.setState({menuChanged:false});
      }    
    }
    disconnect(){
      globals.currentUser=new entity.UserModel(0,'','','');
      // ******we must notify that the menu changed in order to repaint the nituk navbar:this.props.notifyMenuChange();
      this.setState({menuChanged:true})
    }
    render(){
    
    var classForDisconnect=globals.currentUser["un"]==''? 'd-none' : 'visible';
    var disableFlag=globals.currentUser["un"]==''? 'disabled' : '';
    var classForConnect=globals.currentUser["un"]==''? 'visible' : 'd-none';
    return (
      <div>
      <Navbar bg="light" expand="lg" >
      <Navbar.Toggle aria-controls="basic-navbar-nav" />
      <Navbar.Collapse id="basic-navbar-nav">
          
          <Nav className="ml-auto " >
            <Nav.Link disabled={disableFlag} href="#/Delivery"></Nav.Link>
            <Nav.Link disabled={disableFlag} href="#/Input"> </Nav.Link>
            <Nav.Link disabled={disableFlag} href="#/AttachDeAttach"></Nav.Link>
            <Nav.Link disabled={disableFlag} href="#/TablesManage">  </Nav.Link>
            <Nav.Link href="#/About"></Nav.Link>
            <NavDropdown disabled={disableFlag} title="" id="basic-nav-dropdown">
              <NavDropdown.Item href="#/ReportDeviceHistory">  </NavDropdown.Item>
              <NavDropdown.Item href="#/ReportDevicesInUse">  </NavDropdown.Item>
              
              {/* <NavDropdown.Divider /> */}
              
            </NavDropdown>
          </Nav>
          {/* the following nav is redundant */}
          <Nav className="mr-auto border d-none">
            <Nav.Link href="#/Registration/Login"> 1 :</Nav.Link>
            <Nav.Link href="#/Registration/SignUp"> 1 :</Nav.Link>
          </Nav>
          {/* end of the redundant  */}
          <Nav className={"mr-auto " + classForConnect}>
            <Nav.Link href="#/NewLogIn"></Nav.Link>
            <Nav.Link href="#/NewReg"></Nav.Link>
          </Nav>
          
          <Nav className={"mr-auto "+ classForDisconnect} >
            <Nav.Link onClick={this.disconnect} ></Nav.Link>
            <Nav.Link >{globals.currentUser["userDisplayName"]  } <i className="fa fa-user"/> </Nav.Link>
          </Nav>
        </Navbar.Collapse>
      </Navbar>
      
      </div>
    );
  }
  }
  export default Menus;