
import React from 'react';    
import{ Form,Row,Col,Button}  from 'react-bootstrap';
import apple_logo from './appleseeds.png';

class About extends React.Component {
    render(){
       
    return (
      <div className="mx-auto mt-5" style={{width:'30vw',height:'25vx'}} >
        <div className="row">
          <img src={apple_logo} className="mr-auto" ></img>    
        </div>
        <div className="row">
          <h4 style={{borderBottom:'1px solid gray'}} >  </h4>                
        </div>                 
      <section>
        
        <p>     </p>
        <p> 1.1.125  01-01-2019</p>
        <p>  </p>
        <p> :</p>
        <Form>
          <Form.Group as={Row} >
            <Form.Label column sm={2}>
              -
            </Form.Label>
            <Col sm={6}>
              <Form.Control plaintext readOnly defaultValue="email@example.com" />
            </Col>
          </Form.Group>
  
          <Form.Group as={Row} >
            <Form.Label column sm={2}>
              
            </Form.Label>
            <Col sm={4}>
              <Form.Control plaintext readOnly defaultValue="03-7654321" />
            </Col>
          </Form.Group>
          <Button variant="primary"  href="/#" >
            
          </Button>
        </Form>                
      </section>
      </div>
    );
    }
  }
  export default About;